body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto';
  font-size: 16;
  line-height: 1.2; }

.vrtour {
  position: relative;
  width: 100%;
  background: #f3f3f3 url("../img/vrtour-background.jpg");
  background-repeat: no-repeat;
  background-size: cover; }

.vrtour__shadow {
  display: none; }

.vrtour__container {
  display: block; }
  .vrtour__container canvas {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

.vrtour__headline {
  position: relative;
  user-select: none; }
  .vrtour__headline .container {
    padding: 15px;
    max-width: 500px; }
  .vrtour__headline .title {
    font-size: 40px;
    font-weight: 700;
    line-height: 50px;
    text-transform: uppercase;
    color: #333;
    opacity: 0; }
  .vrtour__headline .abstract {
    display: inline-block;
    font-size: 26px;
    font-weight: 700;
    color: #333;
    border-top: 4px solid #333;
    padding-top: 15px;
    padding-right: 50px;
    margin-top: 10px;
    opacity: 0; }

.vrtour {
  min-height: 100vh; }

.vrtour__container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

